.modalContainer {
  width: 75%;
  height: max-content;
  border: 0.2rem solid var(--bubbleGumPink);
  border-radius: 0.2rem;
  background-color: var(--black);
  color: white;
  position: absolute;
  z-index: 1;
  top: 40%;
  left: 10%;
}
img {
  display: block;
  width: 100%;
  height: 10rem;
}
